.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.75);
}
.overlay {
  position: fixed;
  top: 30vh;
  left: 25%;
  width: 50%;
  background-color: white;
  padding: 1rem;
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 30;
  animation-name: top-to-down;
  animation-duration: 1s;
}

@keyframes top-to-down {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
